<template>
	<div class="input-select" @click="SelectClick">
		<slot/>
		<img src="~assets/images/inputs/more.svg" class="imgmore">
	</div>
</template>

<script>


export default {
  name: 'InputSelect',
  components:{
  },
  props:{
	path:{
		type:String,
		default:''
	},
	test:{
		type:Array,
		default(){
			return []
		}
	}
  },
  methods:{
	SelectClick(){
		this.$router.push({name:this.path,params:{arr:this.test}}).catch(res=>{res})
	}
  },
}
</script >

