<template>
	<div class="add-tabbar">
		<div class="add-flex-box" v-if="cartLength">
			<div class="flex-left">
				<i class="add-product-icon" @click="addClick">+</i>
				<div class="cbox" @click="cartHandlerClick">
					<span class="m10">合计:</span>
					<span class="m10">共{{cartLength}}种</span>
					<span class="m10 font12">(数量{{num}})</span>
				</div>
			</div>
			<div class="flex-right" @click="sendClick">提交</div>
		</div>
		<div class="add-icon-btn" @click="addClick" v-else>添加货品</div>
	</div>
</template>

<script>

export default {
  name: 'AddTabbar',
  components: {
  },
  data(){
	return {
		num:0
	}
  },
  methods:{
	addClick(){
		console.log('添加货品')
		//跳转
		this.$router.push("/orderproduct").catch(res=>{res})
	},
	sendClick(){
		//是执行成为了
		this.$emit("successClick")		//往上发送事件
	},
	cartHandlerClick(){
		this.$router.push("/cartlist").catch(err=>{err})
	}
  },
  computed:{
	cartLength(){
		return this.$store.getters.getOrderProductLength
	},
  },
  activated(){
		let res=0
		this.$store.state.orderProductList.forEach(obj=>{
			res+=obj.count
		})
		this.num=res
  }	
}
</script >

